import React, { PropTypes } from 'react';
import styled from 'styled-components';

import {
  primaryColor,
  primaryColorDark,
  primaryColorDarker,
  secondaryColor,
  secondaryColorDark,
  secondaryColorDarker,
  errorColor,
  errorColorDark,
  errorColorDarker,
  successColor,
  successColorDark,
  successColorDarker,
} from './defaultTheme';

function getBgColor(props) {
  let color = primaryColor;
  let colorDark = primaryColorDark;

  // Determine color based on props
  if (props.secondary) {
    color = secondaryColor;
    colorDark = secondaryColorDark;
  }
  if (props.danger) {
    color = errorColor;
    colorDark = errorColorDark;
  }
  if (props.success) {
    color = successColor;
    colorDark = successColorDark;
  }
  if (props.outline || props.clear) {
    color = 'transparent';
    colorDark = 'transparent';
  }

  /* eslint-disable max-len */
  return `background: ${color}; background: linear-gradient(to bottom, ${color} 0%, ${colorDark} 100%);`;
  /* eslint-enable max-len */
}

function getHoverColor(props) {
  let color = primaryColorDark;
  if (props.secondary) color = secondaryColorDark;
  if (props.danger) color = errorColorDark;
  if (props.success) color = successColorDark;
  if (props.clear) color = 'transparent';

  return color;
}

function getActiveColor(props) {
  let color = primaryColorDarker;
  if (props.secondary) color = secondaryColorDarker;
  if (props.danger) color = errorColorDarker;
  if (props.success) color = successColorDarker;

  return color;
}

function getColor(props) {
  if (props.primary) return primaryColor;
  if (props.secondary) return secondaryColor;
  if (props.danger) return errorColor;
  if (props.success) return successColor;
  return primaryColor; // default
}

function getAlignement(props) {
  if (props.align === 'center') return '0px auto';
  if (props.align === 'right') return '0px 0px 0px auto';
  if (props.align === 'left') return '0px auto 0px 0px';
  return '0px';
}

const ButtonWrapper = styled.button`
  outline: none;
  font-size: 1rem;
  padding: 8px 16px;
  font-weight: normal;
  width: ${props => props.w || 'auto'};
  margin: ${props => getAlignement(props)};
  text-shadow: 1px 1px 2px rgba(0,0,0,0.1);
  border-style: solid;
  border-width: ${props => props.outline && !props.clear ? '1px' : '0px'};
  border-radius: 4px;
  border-color: ${props => getColor(props)};
  color: ${props => props.outline || props.clear ? getColor(props) : '#fff'};
  ${props => !props.outline && !props.clear &&
    'box-shadow: 1px 3px 6px rgba(0,0,0,0.1);'
  }
  ${props => getBgColor(props)};
  ${props => props.disabled && 'opacity: 0.5; cursor: not-allowed;'};
  ${props => props.small && 'padding: 4px 8px;'}
  ${props => props.large && 'padding: 12px 20px;'}
  ${props => props.small && 'font-size: 0.8rem;'}
  ${props => props.large && 'font-size: 1.5rem;'}

  &:hover {
    background: ${props => getHoverColor(props)};
    ${props => !props.clear && 'color: #fff;'}
    cursor: pointer;
  }
  &:active {
    background: ${props => !props.clear && getActiveColor(props)};
    color: ${props => props.clear && getActiveColor(props)};
  }
`;

const ButtonContent = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
`;

const Button = ({ children, ...rest }) => (
  <ButtonWrapper {...rest}>
    <ButtonContent>{children}</ButtonContent>
  </ButtonWrapper>
);

Button.propTypes = {
  children: PropTypes.any,
};

export default Button;



